@import url(https://fonts.googleapis.com/css2?family=Asap:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mainWrapper {
  height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(253, 202, 147, 1) 27%
  );
}
.content_main {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.headerheading {
  font-family: "Asap", sans-serif;
  font-size: 65px;
  color: white;
}
.startBtn {
  outline: none !important;
  font-size: 25px;
  background-color: transparent;
  color: white;
  padding: 7px 25px;
  border: 2px solid white;
  border-radius: 10px;
}
.startBtn:hover {
  outline: none !important;
  font-size: 25px;
  background-color: white;
  color: black;
  padding: 7px 25px;
  border: 2px solid white;
  border-radius: 10px;
}

.questionCard_Wrapper {
  height: 100vh;
  width: 100%;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(253, 202, 147, 1) 27%
  );
}
p{
  margin-bottom: 5px !important;
}
.questionContent {
  color: white;
  background: rgb(255, 184, 126);
  background: linear-gradient(
    0deg,
    rgba(255, 184, 126, 1) 0%,
    rgba(253, 148, 142, 1) 100%
  );
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height:400px;
}
.questionStats {
  display: flex;
  justify-content: space-between;
}
.question1 {
  font-size: 25px;
}
.score {
  font-weight: bold;
  font-size: 20px;
}
.questionStats p {
  font-size: 20px;
}
.Hello {
  text-align: center;
  background-color: transparent;
  border: 1px solid white;
  font-size: 18px;
  padding: 10px 0px !important;
}
.isClicked {
  text-align: center;
  background-color: white;
  color: black;
  cursor: pointer;
  border: 1px solid white;
  font-size: 18px;
  padding: 10px 0px !important;
}
.Hello:hover {
  background-color: white;
  color: black;
  cursor: pointer;
}
.nextBtn {
  position: absolute;
  bottom: -3%;
  left: 0%;
  background-color: #fd948d;
  outline: none !important;
  border: none !important;
  font-size: 22px;
  color: white;
  padding: 5px;
}
.nextBtn:hover {
  background-color: #fd948d;
  outline: none !important;
  border: none !important;
  font-size: 22px;
}
.Hello,.isClicked{
  height: 70px;
}
@media (max-width: 768px) {
  .question1 {
    font-size: 25px;
  }
  .questionContent {
    color: white;
    background: rgb(255, 184, 126);
    background: linear-gradient(
      0deg,
      rgba(255, 184, 126, 1) 0%,
      rgba(253, 148, 142, 1) 100%
    );
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 400px;
    height: 500px;
  }
  .isClicked,
  .Hello {
    font-size: 16px;
    height: 90px;
  }
}
